(function() {
  'use strict';

  angular
    .module('web')
    .factory('RenderService', RenderService);

  /** @ngInject */
  function RenderService() {

    if (!Detector.webgl) Detector.addGetWebGLMessage();

    var container;

    var camera, controls, scene, renderer,
      aspectRatio = 16 / 7,
      data = {},
      updatableObjects = [],
      scaleFactor = 100,
      boxColors = [0x33ff33, 0x3333ff, 0xff33ff, 0xffff33, 0x33ffff];

    return {
      start: function () {
        init();
        animate();
      },
      update: function (packingData) {
        data = packingData;
        removeUpdatableObjects();
        addUpdatableObjects();
        render();
      }
    };


    function init() {
      container = document.getElementById('container');

      camera = new THREE.PerspectiveCamera(60, window.innerWidth / window.innerHeight, 1, 1000);
      camera.position.set(100, 100, 100);
      camera.rotation.z = Math.PI / 4;

      controls = new THREE.TrackballControls(camera, container);

      controls.rotateSpeed = 1.0;
      controls.zoomSpeed = 1.2;
      controls.panSpeed = 0.8;

      controls.noZoom = false;
      controls.noPan = false;

      controls.staticMoving = true;
      controls.dynamicDampingFactor = 0.3;

      controls.keys = [65, 83, 68];

      controls.addEventListener('change', render);

      // world

      scene = new THREE.Scene();
      scene.fog = new THREE.FogExp2(0xcccccc, 0.002);
      addPlane();
      addUpdatableObjects();

      // lights

      var light = new THREE.DirectionalLight(0xffffff);
      light.position.set(1, 1, 1);
      scene.add(light);

      light = new THREE.DirectionalLight(0x002288);
      light.position.set(-1, -1, -1);
      scene.add(light);

      light = new THREE.AmbientLight(0x222222);
      scene.add(light);

      //Axis helper
      var axisHelper = new THREE.AxisHelper(30);
      scene.add(axisHelper);


      // renderer

      renderer = new THREE.WebGLRenderer({antialias: false});
      renderer.setClearColor(scene.fog.color);
      renderer.setPixelRatio(window.devicePixelRatio);
      var rect = container.getBoundingClientRect();
      renderer.setSize(rect.width, rect.width / aspectRatio);

      container.appendChild(renderer.domElement);

      //

      window.addEventListener('resize', onWindowResize, false);
      //

      render();

    }

    function addPlane() {
      var texture = new THREE.TextureLoader().load("assets/images/ground1.jpg");
      texture.wrapS = THREE.RepeatWrapping;
      texture.wrapT = THREE.RepeatWrapping;
      texture.repeat.set(25, 25);
      var geometry = new THREE.RingGeometry(0, 500, 16, 1);
      var material = new THREE.MeshBasicMaterial({
        color: 0x8e8e8e,
        map: texture,
        side: THREE.DoubleSide
      });
      var mesh = new THREE.Mesh(geometry, material);
      mesh.rotation.x = -Math.PI / 2;
      mesh.position.y = -0.5;
      scene.add(mesh);
    }

    function addUpdatableObjects() {
      if (data && data.contanerSize && data.placementData) {
        for (var ci = 0; ci < data.placementData.length; ci++) {
          var widthOffset = ci * data.contanerSize[0] / scaleFactor * 1.3;
          addBox([0, 0, 0], data.contanerSize, 0xff3333, true, widthOffset);

          for (var bi = 0; bi < data.placementData[ci].length; bi++) {
            addBox(data.placementData[ci][bi].start, data.placementData[ci][bi].end, boxColors[bi % boxColors.length], false, widthOffset);
          }
        }
      }
    }

    function addBox(starts, ends, color, flipFaces, offset) {
      var width = (ends[0] - starts[0]) / scaleFactor;
      var height = (ends[1] - starts[1]) / scaleFactor;
      var depth = (ends[2] - starts[2]) / scaleFactor;
      var geometry = new THREE.BoxGeometry(width, height, depth);
      var material = new THREE.MeshPhongMaterial({color: color, shading: THREE.SmoothShading});
      if (flipFaces) {
        geometry.scale(-1, -1, -1);
      }

      var mesh = new THREE.Mesh(geometry, material);
      var x = starts[0] / scaleFactor + geometry.parameters.width / 2 + offset;
      var y = starts[1] / scaleFactor + geometry.parameters.height / 2;
      var z = starts[2] / scaleFactor + geometry.parameters.depth / 2;
      mesh.position.set(x, y, z);
      scene.add(mesh);
      updatableObjects.push(mesh);
    }

    function removeUpdatableObjects() {
      updatableObjects.forEach(function (obj) {
        scene.remove(obj);
      });
      updatableObjects = [];
    }

    function onWindowResize() {

      camera.aspect = window.innerWidth / window.innerHeight;
      camera.updateProjectionMatrix();

      var rect = container.getBoundingClientRect();
      renderer.setSize(rect.width, rect.width / aspectRatio);

      controls.handleResize();

      render();

    }

    function animate() {

      requestAnimationFrame(animate);
      controls.update();

    }

    function render() {

      renderer.render(scene, camera);

    }
  }
})();

(function() {
  'use strict';

  angular
    .module('web')
    .factory('CalculationService', CalculationService);

  /** @ngInject */
  function CalculationService($http) {
    var host = 'http://localhost:8080';

    var service = {
      calculatePacking: calculatePacking
    };

    return service;

    function calculatePacking(data) {
      return $http.post(host + '/packing', data)
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('web')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('web')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('web')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('web')
    .directive('d3Section', d3Section)
//test remove after
    .factory('util', function () {
      return {
        uuid: function () {
          return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
            var r = Math.random() * 16 | 0, v = c == 'x' ? r : r & 0x3 | 0x8;
            return v.toString(16);
          });
        }

      }
    });


  /** @ngInject */
  function d3Section() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/d3Section/d3Section.html',
      scope: {
        //creationDate: '='
      },
      controller: D3Controller,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function D3Controller(CalculationService, RenderService, $scope, util) {
      var vm = this;

      $scope.data = {
        products: [
          {
            uuid: util.uuid(),
            code: '1010.23.12',
            length: 1000,
            width: 1000,
            height: 1000
          },
          {
            uuid: util.uuid(),
            code: '1010.23.45',
            length: 1000,
            width: 1500,
            height: 1500
          }
        ]
      };


      vm.paint = paint;

      function paint() {
        CalculationService.calculatePacking($scope.data.products).then(function (response) {
          console.log(response.data);
          RenderService.update(response.data);
        });
      }

      RenderService.start();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('web')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1458385002457;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

